.tools {
	background-color: rgb(255, 250, 240);
	padding: 3px;
	display: flex;
	overflow-x: scroll;
	position: absolute;
	z-index: 99;
	left: 0px;
	right: 0px;
	top: 0;
}

.tools>* {
	margin: 4px;
	padding: 4px;
	border: 1px dashed #ddd;
	border-radius: 3px;
}

.tools img {
	height: 100%;
	padding: 2px;
	background-blend-mode: lighten;
	transition: all .3s;
	border-bottom: solid 2px transparent;
}

.tools .selected {
	border-bottom-color: red;
}

svg {
	display: inline-block;
}

.role-menu-selected {
	font-weight: bold;
	color: dodgerblue;
}

.draw-page {
	position: absolute;
	height: '100vh';
	transition: all 0.3s;
	left: 0;
	right: 0;
	z-index: 99999;
}

.draw-page .toolbar {
	padding: 10px 20px;
	border-bottom: 1px solid #eee;
	margin-top: 20px;
	height: 40px;
	font-size: 20px;
	background-color: #fff;
	border-radius: 20px 20px 0px 0px;
	position: relative;
	overflow: hidden;
}
